import 'cropperjs/dist/cropper.css'
import Cropper from 'cropperjs'

const urlButtonEl = document.querySelector('.urlBtn')
const inputEl = document.querySelector('.url')
const imageEl = document.querySelector('.image')
const previewEl = document.querySelector('.preview')
const downloadButtonEl = document.querySelector('.download')

let cropData

async function getScreenshot(url) {
  const res = await fetch(`http://localhost:4000/video?url=${url}`)
  return res.json()
}

async function getGif() {
  const res = await fetch(`http://localhost:4000/gif`, {
    method: 'POST',
    headers: {
      'Content-Type': 'application/json',
    },
    body: JSON.stringify(cropData),
  })
  return res.json()
}

function setCropper() {
  new Cropper(imageEl, {
    viewMode: 2,
    preview: previewEl,
    background: false,
    zoomable: false,
    crop(event) {
      cropData = this.cropper.getData()
      console.log(cropData)
    },
  })
}

urlButtonEl.addEventListener('click', async (e) => {
  e.preventDefault()
  const screenshot = await getScreenshot(inputEl.value)
  imageEl.src = `data:image/png;base64,${screenshot.image}`
  setCropper()
})

downloadButtonEl.addEventListener('click', async (e) => {
  e.preventDefault()
  const a = await getGif()
})
